import Vue from 'vue'
import * as base from './base'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


Vue.use(ToastPlugin)
Vue.use(AjaxPlugin)

/** 
* @params params | JSON
* @params app | Vm
*
* @return void(0)
*/
export const login = function (params, app) {
base.fetch('/agent/login', params, response => {
let data = response.data
localStorage.clear()
if (data.code !== 0) {
let msg = data.msg || data.error || ''
app.$vux.toast.text(msg, 'middle')
return false
}
let userIdentity = data.data.identity
if (userIdentity.current === 'unknown') {
app.$vux.toast.text('', 'middle')
return false
}
switch (userIdentity.identity) {
case 'curator':
base.setUserInfo('identityName', '')
base.setUserInfo('identityFlag', 'curator')
break
case 'agent':
base.setUserInfo('identityName', '')
base.setUserInfo('identityFlag', 'agent')
break
default:
base.setUserInfo('identityName', '')
base.setUserInfo('identityFlag', 'agent')
}
for (let k in data.data) {
base.setUserInfo('user_' + k, data.data[k])
}

app.$router.push('/')
}, error => {
localStorage.clear()
console.log(error)
})
}

/** 
*/
export const loginOut = function (app) {
base.fetch('/api/v1/agents/logout', {}, response => {
let data = response.data
if (data.code !== 0 && data.code !== -1) {
app.$vux.toast.text(data.error, 'middle')
return false
}
base.deleteUserInfo()
sessionStorage.clear()
sessionStorage.setItem('changeTabbar', 0)
app.$router.push('/login')
return true
}, error => {
console.log(error)
app.$vux.toast.text(error.error, 'middle')
}, 'GET')
}

/** 
*/
export const modifyPassword = function (params, app) {
base.fetch('/api/v1/agents/editPassword', params, response => {
let data = response.data
app.$vux.toast.text(data.msg, 'middle')
if (data.code !== 0) return false
return true
}, error => {
app.$vux.toast.text(error.msg, 'middle')
})
}

/** 
*
* @return void(0)
*/
export const getChessInfo = function (app) {
base.fetch('/api/v1/agents/getChessInfo', {}, response => {
let data = response.data
if (data.code !== 0) {
let msg = data.code || data.error || ''
app.$vux.toast.text(msg, 'middle')
return false
}
data = data.data
for (var k in data) {
base.setUserInfo('chessInfo_' + k, data[k])
}
return true
}, error => {
app.$vux.toast.text(error.msg, 'middle')
}, 'GET')
}

/**  
*
* @return void(0)
*/
export const checkLoginToLogin = function (app) {
base.fetch('/api/v1/agentcards?limit=5&page=1', null, response => {
let data = response.data
let msg = data.msg || data.error || ''
if (data.code === -2 || data.code === -1) {
localStorage.clear()
app.$vux.toast.text(msg, 'middle')
app.$router.push('/login')
}
}, error => {
console.log(error)
}, 'GET')
}



